// Mengganti import 'next/image'
interface ImageProps {
    src: string;
    alt: string;
    fill?: boolean;
    className: string;
    width?: number;
    height?: number;
    priority?: boolean;
}

const Image: React.FC<ImageProps> = ({ src, alt, fill, className, width, height, priority }) => (
  <img 
    src={src} 
    alt={alt} 
    className={className} 
    style={{ 
      objectFit: fill ? 'cover' : undefined, 
      width: fill ? '100%' : (width ? `${width}px` : undefined), 
      height: fill ? '100%' : (height ? `${height * 4 / 3}px` : undefined),
    }} 
    loading={priority ? 'eager' : 'lazy'}
    onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
        e.currentTarget.onerror = null; 
        e.currentTarget.src = 'https://placehold.co/300x400/4b5563/ffffff?text=Image+Not+Found';
    }}
  />
);

// Mengganti import Button (shadcn/ui Button)
interface ButtonBaseProps {
    children: ReactNode;
    variant?: 'primary' | 'outline';
    className?: string;
    disabled?: boolean;
}

// Overload untuk tombol (tanpa href)
type ButtonAsButtonProps = ButtonBaseProps & ButtonHTMLAttributes<HTMLButtonElement> & { href?: never; };

// Overload untuk tautan (dengan href)
type ButtonAsLinkProps = ButtonBaseProps & AnchorHTMLAttributes<HTMLAnchorElement> & { href: string; onClick?: never; };

type ButtonProps = ButtonAsButtonProps | ButtonAsLinkProps;

const Button: React.FC<ButtonProps> = ({ children, variant = 'primary', className = '', disabled, onClick, href, ...props }) => {
  const baseClasses = "inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors disabled:pointer-events-none disabled:opacity-50 h-10 px-4 py-2 shadow-md";
  
  let variantClasses = "bg-indigo-600 text-white hover:bg-indigo-700"; // Default (primary)
  if (variant === 'outline') {
    variantClasses = "border border-indigo-600 text-indigo-600 bg-white hover:bg-indigo-50 transition-colors";
  }

  const finalClasses = `${baseClasses} ${variantClasses} ${className}`;
  
  if (href) {
    // Render sebagai <a>
    return (
        <a 
            href={href} 
            target="_blank" 
            rel="noopener noreferrer" 
            className={finalClasses}
            {...(props as AnchorHTMLAttributes<HTMLAnchorElement>)}
        >
            {children}
        </a>
    );
  }
  
  // Render sebagai <button>
  return (
    <button 
        className={finalClasses} 
        disabled={disabled} 
        onClick={onClick as React.MouseEventHandler<HTMLButtonElement>}
        {...(props as ButtonHTMLAttributes<HTMLButtonElement>)}
    >
        {children}
    </button>
  );
};

