// --- DEFINISI TIPE (Interfaces, Sesuai Jikan v4) ---

interface JikanReference { mal_id: number; name: string; url?: string; }
interface Relation { relation: string; entry: JikanReference[]; }
interface StaffEntry { person: JikanReference; position: string; }
interface VoiceActor { person: JikanReference; language: string; }
interface CharacterData {
  character: { mal_id: number; name: string; images: { jpg: { image_url: string; } } };
  role: string;
  voice_actors: VoiceActor[]; 
}

interface Anime {
  mal_id: number;
  title: string;
  title_english: string | null;
  score: number | null;
  episodes: number | null;
  status: string | null;
  year: number | null;
  synopsis: string | null;
  source: string | null;
  members: number | null; 
  popularity: number | null; 
  
  images: {
    jpg: {
      large_image_url: string;
    }
  };

  aired: { string: string | null; from: string | null; to: string | null; } | null;
  trailer: { embed_url: string | null; url: string | null; } | null;

  genres: JikanReference[];
  relations: Relation[];
  studios: JikanReference[]; 
  staff: StaffEntry[]; 
  characters: CharacterData[]; 
}

// --- FUNGSI API JIKAN SEBENARNYA ---
const JIKAN_BASE_URL = 'https://api.jikan.moe/v4/anime/';

/**
 * Mengambil detail anime dari Jikan API menggunakan ID.
 * @param id ID MAL anime
 * @returns Promise yang berisi objek Anime atau null
 */
async function getAnimeDetail(id: number): Promise<Anime | null> {
  const url = `${JIKAN_BASE_URL}${id}/full`;
  console.log(`Fetching anime details from: ${url}`);
  
  try {
    const response = await fetch(url);
    
    // Jikan API mengembalikan status 404 jika ID tidak ditemukan
    if (response.status === 404) {
      console.error(`Anime with ID ${id} not found (404).`);
      return null;
    }

    if (!response.ok) {
      throw new Error(`Failed to fetch anime data: ${response.statusText}`);
    }

    const json = await response.json();
    // Jikan API membungkus data dalam properti 'data'
    return json.data as Anime;
    
  } catch (error) {
    console.error('Error fetching data from Jikan API:', error);
    return null; // Mengembalikan null pada kegagalan API
  }
}
// --- END API FUNCTION ---

// --- UTILITY FUNCTIONS ---
const formatNumber = (num: number | null): string => {
  if (num === null) return 'N/A';
  return num.toLocaleString('en-US');
}

const getTrailerEmbedUrl = (trailer: Anime['trailer']): string | null => {
    // Jikan API v4 biasanya memberikan embed_url, kita ambil itu
    if (trailer?.embed_url) {
        return trailer.embed_url;
    }
    // Fallback jika hanya URL biasa yang tersedia
    if (trailer?.url && trailer.url.includes('youtube.com')) {
        // Coba konversi URL YouTube biasa ke format embed (sangat sederhana)
        try {
            const url = new URL(trailer.url);
            const videoId = url.searchParams.get('v');
            if (videoId) {
                return `https://www.youtube.com/embed/${videoId}`;
            }
        } catch (e) {
            return trailer.url;
        }
    }
    return null;
}

